@media screen and (min-width: 1440px) {
    .filters-rankings {
        margin-left: 80px;
        margin-right: 80px;
        display: flex;
        flex-direction: row;
        margin-bottom: 40px;
        gap: 24px;
        margin-top: 80px;
    }

    #table-container .loader {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        display: inline-block;
        position: relative;
        background: linear-gradient(0deg, #675F99 33%, #AB9EFF 100%);
        box-sizing: border-box;
        animation: rotation 1s linear infinite;
        align-self: center;
        justify-self: center;
        left: 50%;
        margin-top: 100px;
    }

    #table-container .loader::after {
        content: '';  
        box-sizing: border-box;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 44px;
        height: 44px;
        border-radius: 50%;
        background: #211D27;
        }
        @keyframes rotation {
        0% { transform: rotate(0deg) }
        100% { transform: rotate(360deg)}
    } 

    .share {
        height: 40px;
        color: #ffffff;
        border-radius: 20px;
        border: none;
        font-size: 18px;
        font-weight: 400;
        justify-self: end;
        margin-left: auto;
        cursor: pointer;
        transition: all .2s ease-in-out;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 8px
    }

    .button-text {
        margin-block-end: 0;
        margin-block-start: 0;
    }

    .MuiButton-root {
        text-transform: none;
    }

    #filter-button {
        display: none;
    }

}


@media screen and (max-width: 500px) {
    .filters-rankings {
        margin-left: 24px;
        margin-right: 24px;
        display: flex;
        flex-direction: row;
        margin-bottom: 24px;
        gap: 24px;
        margin-top: 16px;
        justify-content: space-between;
        align-items: center;
    }

    #type-select {
        display: none;
    }

    #table-container .loader {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        display: inline-block;
        position: relative;
        background: linear-gradient(0deg, #675F99 33%, #AB9EFF 100%);
        box-sizing: border-box;
        animation: rotation 1s linear infinite;
        align-self: center;
        justify-self: center;
        left: 50%;
        margin-top: 100px;
    }

    #table-container .loader::after {
        content: '';  
        box-sizing: border-box;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 44px;
        height: 44px;
        border-radius: 50%;
        background: #211D27;
        }
        @keyframes rotation {
        0% { transform: rotate(0deg) }
        100% { transform: rotate(360deg)}
    } 

    .share {
        height: 60px;
        color: #ffffff;
        border-radius: 20px;
        border: none;
        font-size: 18px;
        font-weight: 400;
        justify-self: end;
        margin-left: auto;
        cursor: pointer;
        transition: all .2s ease-in-out;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 8px;
        position: fixed;
        bottom: 16px;
        right: 16px;
        z-index: 100;
    }

    .button-text {
        margin-block-end: 0;
        margin-block-start: 0;
    }

    .MuiButton-root {
        text-transform: none;
    }

    #share {
        text-transform: none;
        height: 48px !important;
        border-radius: 24px !important;
        padding-inline: 24px !important;
    }

    .filter-drawer {
        position: fixed;
        top: 100%;
        bottom: 0;
        width: 100%;
        height: -webkit-fill-available;
        background-color: #211D27;
        border-radius: 20px 20px 0 0;
        transition: all 0.3s ease-out;
        z-index: 1000;
      }
    
      .filter-drawer-content {
        padding: 40px 24px 0px 24px;
        height: 100%;
      }
    
      .filter-drawer-header {
        display: flex;
        justify-content: space-between;
        width: 100%;
        align-items: center;
      }
    
      .filter-drawer-header-title {
        justify-self: center;
        margin-left: 40px;
        font-size: 20px;
        color: #CFCFCF;
        font-weight: 600;
      }
    
      .filter-drawer-body {
        margin-top: 40px;
        display: flex;
        flex-direction: column;
        justify-content: baseline;
        align-items: stretch;
        width: 100%;
        height: -webkit-fill-available;
        overflow: auto;
        gap: 24px;
      }
    
      #artist-filter-drawer .searchWrapper {
        min-height: 40px !important;
        height: 48px;
        min-width: 108px;
        border: 1px solid #B1A5FA;
        border-radius: 24px !important;
        max-width: 100%;
        align-content: center;
        padding-left: 12px !important;
        align-items: center;
        transition: all 0.5s;
        margin-bottom: 8px;
      }
      
      #artist-filter-drawer .optionContainer {
        font-size: 16px;
        max-height: 184px;
      }
    
      #artist-filter-drawer .icon_down_dir {
        right: 24px;
      }
    
      #artist-filter-drawer_input {
        margin-top: 0px;
      }
    
      #artist-filter-drawer .chip {
        border-radius: 20px !important;
        background-color: #897ECC !important;
        margin-bottom: 2px !important;
        color: #FFFFFF !important;
      }
    
      #artist-filter-drawer .option:hover {
        background-color: #897ECC50 !important;
        color: #CFCFCF !important;
    }
    
      #year-filter-drawer {
        width: 100%;
      }
    
      #year-filter-drawer .year-filter-container {
        max-width: 100%;
        height: 48px;
        border-radius: 24px;
      }
    
      #year-filter-drawer .calendar-wrapper {
        width: calc(100% - 48px);
        padding-right: 0px;
        position: absolute;
        left: 24px;
        right: 24px;
      }
    
      #year-filter-drawer .carousal {
        width: 100%;
      }
    
      #year-filter-drawer .chip-wrapper {
        padding-bottom: 0px;
        padding-top: 5px;
        padding-left: 12px;
      }
    
      #year-filter-drawer-container {
        align-items: center;
      }
    
      #year-filter-drawer .MuiSvgIcon-root {
        margin-right: 8px !important;
      }
    
      hr {
        width: 100%;
        border-color: #CFCFCF50;
      }

}


